          <v-container>
            <div class="my-5">Settings</div>

            <div>Positive</div>
            <v-row>
              <v-col cols="4">
                up : <b class="text-h6">{{ orderModel.positive_up_index }}</b>
              </v-col>
              <v-col cols="4">
                down : <b class="text-h6">{{ orderModel.positive_down_index }}</b>
              </v-col>
            </v-row>

            <div class="mt-3">Negative</div>
            <v-row>
              <v-col cols="4">
                up : <b class="text-h6">{{ orderModel.negative_up_index }}</b>
              </v-col>
              <v-col cols="4">
                down : <b class="text-h6">{{ orderModel.negative_down_index }}</b>
